import api from './api';

//     
const userService = {
  //   
  getCurrentUser: async () => {
    console.log('[User Service]    ');
    try {
      const response = await api.get('/users/me');
      console.log('[User Service]   :', response.data);
      return response;
    } catch (error) {
      console.error('[User Service]    :', error);
      throw error;
    }
  },
  
  //   
  updateUserProfile: async (userData) => {
    console.log('[User Service]   :', userData);
    
    //    API
    const backendData = {
      first_name: userData.firstName,
      last_name: userData.lastName,
      email: userData.email,
      phone: userData.phone,
      birth_date: userData.birthDate
    };
    
    console.log('[User Service]     :', backendData);
    return api.put('/users/me', backendData);
  },
  
  //  
  changePassword: async (oldPassword, newPassword) => {
    console.log('[User Service]    ');
    return api.put('/users/me/password', {
      old_password: oldPassword,
      new_password: newPassword
    });
  }
};

export default userService; 