import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { useAppDispatch } from '../store/hooks';
import { getUserProfile, updateUserProfile } from '../store/auth/authSlice';
import Spinner from '../components/ui/Spinner';
import './ProfilePage.css';

const ProfilePage: React.FC = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const authState = useSelector((state: any) => state.auth);
  const { user, loading, error, isAuthenticated } = authState;
  
  //    
  useEffect(() => {
    console.log('[ProfilePage]  :', {
      isAuthenticated: authState.isAuthenticated,
      hasUser: !!authState.user,
      hasToken: !!authState.token,
      loading: authState.loading,
      error: authState.error
    });
    
    if (authState.user) {
      console.log('[ProfilePage]  :', JSON.stringify(authState.user, null, 2));
    }
  }, [authState]);
  
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    phone: '',
    birthDate: ''
  });
  
  const [isEditing, setIsEditing] = useState(false);
  const [formErrors, setFormErrors] = useState<Record<string, string>>({});
  
  useEffect(() => {
    console.log('[ProfilePage]  :', isAuthenticated);
    if (!isAuthenticated) {
      console.log('[ProfilePage]    ');
      navigate('/login', { state: { from: '/profile' } });
      return;
    }
    
    //   
    console.log('[ProfilePage]   ');
    dispatch(getUserProfile());
  }, [dispatch, navigate, isAuthenticated]);
  
  useEffect(() => {
    console.log('[ProfilePage]     :', user);
    if (user) {
      try {
        //   
        let fullName = user.fullName || '';
        
        if (!fullName) {
          if (user.first_name || user.firstName) {
            //        (    )
            const firstName = user.first_name || user.firstName || '';
            const lastName = user.last_name || user.lastName || '';
            fullName = `${firstName} ${lastName}`.trim();
            console.log('[ProfilePage]     first_name  last_name:', fullName);
          }
        }
        
        //  email, phone  birthDate    (    )
        const email = user.email || '';
        const phone = user.phone || '';
        const birthDate = user.birth_date || user.birthDate || '';
        
        console.log('[ProfilePage]   :', {
          fullName,
          email,
          phone,
          birthDate
        });
        
        //       
        setFormData({
          fullName: fullName,
          email: email,
          phone: phone,
          birthDate: birthDate ? (birthDate.includes('T') ? birthDate.split('T')[0] : birthDate) : ''
        });
      } catch (err) {
        console.error('[ProfilePage]     :', err);
      }
    }
  }, [user]);
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    //     
    if (formErrors[name]) {
      setFormErrors(prev => {
        const newErrors = { ...prev };
        delete newErrors[name];
        return newErrors;
      });
    }
  };
  
  const validateForm = (): boolean => {
    const errors: Record<string, string> = {};
    
    if (!formData.fullName.trim()) {
      errors.fullName = '  ';
    }
    
    if (!formData.email.trim()) {
      errors.email = ' email';
    } else if (!/^\S+@\S+\.\S+$/.test(formData.email)) {
      errors.email = '  email';
    }
    
    if (formData.phone && !/^\+?\d{10,15}$/.test(formData.phone)) {
      errors.phone = '   ';
    }
    
    setFormErrors(errors);
    return Object.keys(errors).length === 0;
  };
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log('[ProfilePage]  ');
    
    if (validateForm()) {
      console.log('[ProfilePage]   ');
      //        ( API)
      const nameParts = formData.fullName.trim().split(' ');
      const firstName = nameParts[0] || '';
      const lastName = nameParts.slice(1).join(' ') || '';
      
      const profileData = {
        firstName,
        lastName,
        email: formData.email,
        phone: formData.phone,
        birthDate: formData.birthDate
      };
      
      console.log('[ProfilePage]    :', profileData);
      
      //     
      dispatch(updateUserProfile(profileData))
        .unwrap()
        .then((result) => {
          console.log('[ProfilePage]   :', result);
          //     
          alert('  !');
          setIsEditing(false);
        })
        .catch((error) => {
          console.error('[ProfilePage]   :', error);
          //    
          alert(`  : ${error}`);
        });
    } else {
      console.error('[ProfilePage]    ');
    }
  };
  
  const handleEdit = () => {
    setIsEditing(true);
  };
  
  const handleCancel = () => {
    //     
    if (user) {
      setFormData({
        fullName: user.fullName || '',
        email: user.email || '',
        phone: user.phone || '',
        birthDate: user.birthDate ? new Date(user.birthDate).toISOString().split('T')[0] : ''
      });
    }
    setIsEditing(false);
    setFormErrors({});
  };
  
  if (loading) {
    return <Spinner />;
  }
  
  if (error) {
    return (
      <div className="profile-error">
        <h2>  </h2>
        <p>{error}</p>
      </div>
    );
  }
  
  return (
    <div className="profile-page">
      <div className="profile-hero">
        <div className="container">
          <h1> </h1>
          <p className="profile-subtitle">    </p>
        </div>
      </div>
      
      <div className="profile-container">
        <div className="profile-sidebar">
          <div className="profile-menu">
            <button className="menu-item active"></button>
            <button className="menu-item" onClick={() => navigate('/orders')}> </button>
            <button className="menu-item" onClick={() => navigate('/support')}></button>
          </div>
        </div>
        
        <div className="profile-content">
          <div className="profile-header">
            <h2> </h2>
            {!isEditing && (
              <button className="edit-button" onClick={handleEdit}>
                
              </button>
            )}
          </div>
          
          <form className="profile-form" onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="username"></label>
              <input
                type="text"
                id="username"
                name="username"
                value={user?.username || ''}
                disabled={true}
                className="readonly-field"
              />
              <small className="field-hint">    </small>
            </div>
            
            <div className="form-group">
              <label htmlFor="fullName"> </label>
              <input
                type="text"
                id="fullName"
                name="fullName"
                value={formData.fullName}
                onChange={handleChange}
                disabled={!isEditing}
                className={formErrors.fullName ? 'error' : ''}
              />
              {formErrors.fullName && <div className="error-message">{formErrors.fullName}</div>}
            </div>
            
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <input
                type="email"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                disabled={!isEditing}
                className={formErrors.email ? 'error' : ''}
              />
              {formErrors.email && <div className="error-message">{formErrors.email}</div>}
            </div>
            
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="phone"></label>
                <input
                  type="tel"
                  id="phone"
                  name="phone"
                  value={formData.phone}
                  onChange={handleChange}
                  disabled={!isEditing}
                  className={formErrors.phone ? 'error' : ''}
                />
                {formErrors.phone && <div className="error-message">{formErrors.phone}</div>}
              </div>
              
              <div className="form-group">
                <label htmlFor="birthDate"> </label>
                <input
                  type="date"
                  id="birthDate"
                  name="birthDate"
                  value={formData.birthDate}
                  onChange={handleChange}
                  disabled={!isEditing}
                />
              </div>
            </div>
            
            {isEditing && (
              <div className="form-actions">
                <button type="button" className="cancel-button" onClick={handleCancel}>
                  
                </button>
                <button type="submit" className="save-button">
                  
                </button>
              </div>
            )}
          </form>
          
          <div className="password-change-section">
            <h3> </h3>
            <button className="change-password-button" onClick={() => navigate('/change-password')}>
               
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfilePage; 