import React, { useState, useEffect } from 'react';
import { useNavigate, Link, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../store/auth/authSlice';
import './LoginPage.css';

const LoginPage: React.FC = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();
  const authState = useSelector((state: any) => state.auth);
  const isAuthenticated = authState.isAuthenticated;
  
  //    
  useEffect(() => {
    console.log('[LoginPage]  :', {
      isAuthenticated: authState.isAuthenticated,
      hasUser: !!authState.user,
      hasToken: !!authState.token,
      loading: authState.loading,
      error: authState.error
    });
    
    if (authState.user) {
      console.log('[LoginPage]  :', JSON.stringify(authState.user, null, 2));
    }
  }, [authState]);
  
  //    ,   
  useEffect(() => {
    if (isAuthenticated) {
      const from = (location.state as any)?.from || '/profile';
      console.log('[LoginPage]  :', from);
      navigate(from, { replace: true });
    }
  }, [isAuthenticated, navigate, location]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!username || !password) {
      setError(',   ');
      return;
    }
    
    console.log('[LoginPage]   ');
    setLoading(true);
    setError('');
    
    try {
      console.log('[LoginPage]    ');
      const result = await dispatch(login({ usernameOrEmail: username, password }) as any);
      console.log('[LoginPage]  :', result.meta.requestStatus);
      
      if (result.meta.requestStatus === 'fulfilled') {
        console.log('[LoginPage]  ');
        //   -    useEffect
      } else if (result.payload) {
        console.error('[LoginPage]  :', result.payload);
        //     
        setError(String(result.payload));
        
        //     
        setTimeout(() => {
          if (loading) setLoading(false);
        }, 100);
      }
    } catch (err: any) {
      console.error('[LoginPage]   :', err);
      setError(err.message || ' . ,  .');
    } finally {
      setLoading(false);
      console.log('[LoginPage]   ');
    }
  };

  return (
    <div className="login-container">
      <div className="login-form-wrapper">
        <h1>  </h1>
        
        {error && <div className="error-message">{error}</div>}
        
        <form onSubmit={handleSubmit} className="login-form">
          <div className="form-group">
            <label htmlFor="username"> </label>
            <input
              type="text"
              id="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              disabled={loading}
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="password"></label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              disabled={loading}
            />
          </div>
          
          <button type="submit" className="login-button" disabled={loading}>
            {loading ? '...' : ''}
          </button>
        </form>
        
        <div className="login-links">
          <p>
              ? <Link to="/register"></Link>
          </p>
        </div>
      </div>
    </div>
  );
};

export default LoginPage; 