import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

const AdminHotels: React.FC = () => {
  const dispatch = useDispatch();
  const [hotels, setHotels] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [selectedHotel, setSelectedHotel] = useState<any>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  
  useEffect(() => {
    //     
    //      API
    setLoading(true);
    
    //   
    setTimeout(() => {
      setHotels([
        { id: 1, name: ' ', city: { id: 1, name: '' }, category: 5, address: '. , 15' },
        { id: 2, name: ' ', city: { id: 2, name: ' ' }, category: 4, address: '. , 42' },
      ]);
      setLoading(false);
    }, 800);
  }, []);
  
  const handleAddHotel = () => {
    setSelectedHotel(null);
    setIsModalOpen(true);
  };
  
  const handleEditHotel = (hotel: any) => {
    setSelectedHotel(hotel);
    setIsModalOpen(true);
  };
  
  const handleDeleteHotel = (hotelId: number) => {
    if (window.confirm(' ,     ?')) {
      //  
      // dispatch(deleteHotel(hotelId));
      setHotels(hotels.filter(hotel => hotel.id !== hotelId));
    }
  };
  
  const handleSaveHotel = (hotelData: any) => {
    if (selectedHotel) {
      //   
      // dispatch(updateHotel({ ...hotelData, id: selectedHotel.id }));
      setHotels(hotels.map(hotel => 
        hotel.id === selectedHotel.id ? { ...hotel, ...hotelData } : hotel
      ));
    } else {
      //   
      // dispatch(createHotel(hotelData));
      const newHotel = { 
        id: hotels.length ? Math.max(...hotels.map(h => h.id)) + 1 : 1,
        ...hotelData
      };
      setHotels([...hotels, newHotel]);
    }
    setIsModalOpen(false);
  };
  
  const handleCloseModal = () => {
    setIsModalOpen(false);
  };
  
  return (
    <div className="admin-hotels">
      <div className="admin-header">
        <h2> </h2>
        <button className="admin-btn admin-btn-primary" onClick={handleAddHotel}>
           
        </button>
      </div>
      
      {loading ? (
        <div className="admin-loading">...</div>
      ) : error ? (
        <div className="admin-error">{error}</div>
      ) : (
        <div className="admin-table-container">
          <table className="admin-table">
            <thead>
              <tr>
                <th>ID</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {hotels.map(hotel => (
                <tr key={hotel.id}>
                  <td>{hotel.id}</td>
                  <td>{hotel.name}</td>
                  <td>{hotel.city?.name}</td>
                  <td>{"".repeat(hotel.category)}</td>
                  <td>{hotel.address}</td>
                  <td className="admin-actions">
                    <button 
                      className="admin-btn admin-btn-sm"
                      onClick={() => handleEditHotel(hotel)}
                    >
                      
                    </button>
                    <button 
                      className="admin-btn admin-btn-sm admin-btn-danger"
                      onClick={() => handleDeleteHotel(hotel.id)}
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
      
      {/*    /  */}
      {isModalOpen && (
        <div className="admin-modal">
          <div className="admin-modal-content">
            <h3>{selectedHotel ? ' ' : ' '}</h3>
            {/*   /  */}
            <div className="admin-modal-footer">
              <button className="admin-btn" onClick={handleCloseModal}></button>
              <button 
                className="admin-btn admin-btn-primary" 
                onClick={() => handleSaveHotel({})}
              >
                
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminHotels; 