import React from 'react';
import './OrderStatus.css';

interface OrderStatusProps {
  status: string;
}

const OrderStatus: React.FC<OrderStatusProps> = ({ status }) => {
  //       
  const getStatusText = (status: string): string => {
    switch (status) {
      case 'PENDING':
        return ' ';
      case 'CONFIRMED':
        return '';
      case 'PROCESSING':
        return '';
      case 'COMPLETED':
        return '';
      case 'CANCELLED':
        return '';
      default:
        return ' ';
    }
  };
  
  //        
  const getStatusClass = (status: string): string => {
    switch (status) {
      case 'PENDING':
        return 'pending';
      case 'CONFIRMED':
        return 'confirmed';
      case 'PROCESSING':
        return 'processing';
      case 'COMPLETED':
        return 'completed';
      case 'CANCELLED':
        return 'cancelled';
      default:
        return 'unknown';
    }
  };
  
  return (
    <div className={`order-status ${getStatusClass(status)}`}>
      {getStatusText(status)}
    </div>
  );
};

export default OrderStatus; 