import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { authService } from '../../services/api';
import userService from '../../services/userService';

//  
export const login = createAsyncThunk(
  'auth/login',
  async (credentials, { rejectWithValue, dispatch }) => {
    try {
      console.log('[Auth JS]    ');
      const response = await authService.login(credentials);
      console.log('[Auth JS]    :', response.status);
      console.log('[Auth JS]  :', JSON.stringify(response.data, null, 2));
      
      //      
      const accessToken = response.data.accessToken;
      const refreshToken = response.data.refreshToken;
      
      if (!accessToken || !refreshToken) {
        console.error('[Auth JS]    ');
        return rejectWithValue('     ');
      }
      
      //    localStorage
      console.log('[Auth JS]    localStorage');
      localStorage.setItem('accessToken', accessToken);
      localStorage.setItem('refreshToken', refreshToken);
      localStorage.setItem('token', accessToken); //  
      
      //   
      console.log('[Auth JS]   ');
      dispatch(getUserProfile());
      
      return { 
        accessToken, 
        refreshToken 
      };
    } catch (error) {
      console.error('[Auth JS]  :', error);
      return rejectWithValue(error.response?.data?.message || '  ');
    }
  }
);

export const register = createAsyncThunk(
  'auth/register',
  async (userData, { rejectWithValue }) => {
    try {
      const response = await authService.register(userData);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response?.data?.message || '  ');
    }
  }
);

export const logout = createAsyncThunk(
  'auth/logout',
  async (_, { rejectWithValue }) => {
    try {
      await authService.logout();
      return;
    } catch (error) {
      return rejectWithValue(error.response?.data?.message || '  ');
    }
  }
);

export const getUserProfile = createAsyncThunk(
  'auth/getUserProfile',
  async (_, { rejectWithValue }) => {
    try {
      console.log('[Auth JS]    ');
      //  userService  authService
      const response = await userService.getCurrentUser();
      console.log('[Auth JS]       ');
      
      if (!response.data) {
        console.error('[Auth JS]     ');
        return rejectWithValue('     ');
      }
      
      console.log('[Auth JS]  :', JSON.stringify(response.data, null, 2));
      return response.data;
    } catch (error) {
      console.error('[Auth JS]     :', error);
      return rejectWithValue(error.response?.data?.message || '   ');
    }
  }
);

export const updateUserProfile = createAsyncThunk(
  'auth/updateUserProfile',
  async (userData, { rejectWithValue }) => {
    try {
      console.log('[Auth JS]   :', userData);
      
      //  userService  authService
      const response = await userService.updateUserProfile(userData);
      console.log('[Auth JS]    :', response.status, response.data);
      
      //      
      const userResponse = await userService.getCurrentUser();
      console.log('[Auth JS]    :', userResponse.data);
      
      return userResponse.data;
    } catch (error) {
      console.error('[Auth JS]    :', error);
      return rejectWithValue(error.response?.data?.message || '   ');
    }
  }
);

export const checkAdminAccess = createAsyncThunk(
  'auth/checkAdminAccess',
  async (_, { rejectWithValue, getState }) => {
    try {
      console.log('[Auth JS]   ');
      
      //     
      const state = getState();
      const { user } = state.auth;
      
      if (!user) {
        console.error('[Auth JS]       ');
        return rejectWithValue('  ');
      }
      
      //   
      const isAdmin = user.role && user.role.name === 'admin';
      console.log('[Auth JS]    :', isAdmin);
      
      return { isAdmin };
    } catch (error) {
      console.error('[Auth JS]     :', error);
      return rejectWithValue(error.response?.data?.message || '   ');
    }
  }
);

//  
const initialState = {
  user: null,
  token: localStorage.getItem('token') || null,
  isAuthenticated: !!localStorage.getItem('token'),
  isAdmin: false,
  loading: false,
  error: null
};

// Slice
const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    clearError: (state) => {
      state.error = null;
    },
    setToken: (state, action) => {
      state.token = action.payload;
      state.isAuthenticated = !!action.payload;
      if (action.payload) {
        localStorage.setItem('token', action.payload);
      } else {
        localStorage.removeItem('token');
      }
    }
  },
  extraReducers: (builder) => {
    builder
      //   login
      .addCase(login.pending, (state) => {
        console.log('[Auth JS Reducer] login.pending');
        state.loading = true;
        state.error = null;
      })
      .addCase(login.fulfilled, (state, action) => {
        console.log('[Auth JS Reducer] login.fulfilled, :', JSON.stringify(action.payload, null, 2));
        state.loading = false;
        //     localStorage  thunk
        state.token = action.payload.accessToken;
        //   user  isAuthenticated 
        //     getUserProfile.fulfilled
      })
      .addCase(login.rejected, (state, action) => {
        console.error('[Auth JS Reducer] login.rejected:', action.payload);
        state.loading = false;
        state.error = action.payload;
      })
      
      //   register
      .addCase(register.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(register.fulfilled, (state) => {
        state.loading = false;
      })
      .addCase(register.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      
      //   logout
      .addCase(logout.pending, (state) => {
        state.loading = true;
      })
      .addCase(logout.fulfilled, (state) => {
        state.loading = false;
        state.isAuthenticated = false;
        state.user = null;
        state.token = null;
        state.isAdmin = false;
        localStorage.removeItem('token');
      })
      .addCase(logout.rejected, (state) => {
        state.loading = false;
        state.isAuthenticated = false;
        state.user = null;
        state.token = null;
        state.isAdmin = false;
        localStorage.removeItem('token');
      })
      
      //   getUserProfile
      .addCase(getUserProfile.pending, (state) => {
        console.log('[Auth JS Reducer] getUserProfile.pending');
        state.loading = true;
        state.error = null;
      })
      .addCase(getUserProfile.fulfilled, (state, action) => {
        console.log('[Auth JS Reducer] getUserProfile.fulfilled, :', JSON.stringify(action.payload, null, 2));
        state.loading = false;
        //   
        if (!action.payload) {
          console.error('[Auth JS Reducer] getUserProfile.fulfilled   ');
          state.error = '   ';
          return;
        }
        
        //   
        if (!action.payload.role) {
          console.error('[Auth JS Reducer]     ');
          action.payload.role = { id: 0, name: 'user' }; //    
        }
        
        state.user = action.payload;
        state.isAuthenticated = true; //      
        state.isAdmin = action.payload.role && action.payload.role.name === 'admin';
        
        console.log('[Auth JS Reducer]   getUserProfile.fulfilled:', {
          isAuthenticated: state.isAuthenticated,
          isAdmin: state.isAdmin,
          hasUser: !!state.user
        });
      })
      .addCase(getUserProfile.rejected, (state, action) => {
        console.error('[Auth JS Reducer] getUserProfile.rejected:', action.payload);
        state.loading = false;
        state.error = action.payload;
      })
      
      //   updateUserProfile
      .addCase(updateUserProfile.pending, (state) => {
        console.log('[Auth JS Reducer] updateUserProfile.pending');
        state.loading = true;
        state.error = null;
      })
      .addCase(updateUserProfile.fulfilled, (state, action) => {
        console.log('[Auth JS Reducer] updateUserProfile.fulfilled, :', JSON.stringify(action.payload, null, 2));
        state.loading = false;
        
        //   
        if (!action.payload) {
          console.error('[Auth JS Reducer] updateUserProfile.fulfilled   ');
          state.error = '   ';
          return;
        }
        
        //   
        if (!action.payload.role) {
          console.error('[Auth JS Reducer]      ');
          action.payload.role = { id: 0, name: 'user' }; //    
        }
        
        //  fullName  first_name  last_name,  fullName  
        if (!action.payload.fullName && (action.payload.first_name || action.payload.firstName)) {
          const firstName = action.payload.first_name || action.payload.firstName || '';
          const lastName = action.payload.last_name || action.payload.lastName || '';
          action.payload.fullName = `${firstName} ${lastName}`.trim();
          console.log('[Auth JS Reducer]  fullName:', action.payload.fullName);
        }
        
        state.user = action.payload;
        console.log('[Auth JS Reducer]   updateUserProfile.fulfilled:', {
          hasUser: !!state.user,
          fullName: state.user.fullName,
          firstName: state.user.first_name || state.user.firstName,
          lastName: state.user.last_name || state.user.lastName
        });
      })
      .addCase(updateUserProfile.rejected, (state, action) => {
        console.error('[Auth JS Reducer] updateUserProfile.rejected:', action.payload);
        state.loading = false;
        state.error = action.payload;
      })
      
      //   checkAdminAccess
      .addCase(checkAdminAccess.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(checkAdminAccess.fulfilled, (state, action) => {
        state.loading = false;
        state.isAdmin = action.payload.isAdmin;
      })
      .addCase(checkAdminAccess.rejected, (state) => {
        state.loading = false;
        state.isAdmin = false;
      });
  }
});

export const { clearError, setToken } = authSlice.actions;

export default authSlice.reducer; 