import React, { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useAppDispatch } from '../../store/hooks';
import { logout, getUserProfile } from '../../store/auth/authSlice';
import './Header.css';

const Header: React.FC = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const location = useLocation();
  const authState = useSelector((state: any) => state.auth);
  const { user, isAuthenticated } = authState;
  const [mobileMenuOpen, setMobileMenuOpen] = useState<boolean>(false);
  const [scrolled, setScrolled] = useState<boolean>(false);

  //     
  useEffect(() => {
    if (isAuthenticated && !user) {
      dispatch(getUserProfile());
    }
  }, [isAuthenticated, user, dispatch]);

  //        
  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 20) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  //      
  useEffect(() => {
    setMobileMenuOpen(false);
  }, [location]);

  //     
  const handleLogout = () => {
    dispatch(logout());
    navigate('/');
  };

  // ,   
  const isActive = (path: string): boolean => {
    return location.pathname === path;
  };

  //   
  const getFullName = (): string => {
    if (!user) return '';
    
    //  1: fullName
    if (user.fullName) {
      return user.fullName;
    }
    
    //  2: first_name + last_name
    if (user.first_name || user.firstName) {
      const firstName = user.first_name || user.firstName || '';
      const lastName = user.last_name || user.lastName || '';
      if (firstName || lastName) {
        return `${firstName} ${lastName}`.trim();
      }
    }
    
    //  3: username
    return user.username || '';
  };

  //    
  const defaultAvatar = '/images/default-avatar.png';

  return (
    <header className={`header ${scrolled ? 'scrolled' : ''}`}>
      <div className="container header-container">
        <div className="header-logo">
          <Link to="/">
            <div className="header-logo-text">
              
              <span>  </span>
            </div>
          </Link>
        </div>
        
        <nav className="header-nav">
          <ul className="header-menu">
            <li className="header-menu-item">
              <Link to="/" className={isActive('/') ? 'active' : ''}>
                
              </Link>
            </li>
            
            <li className="header-menu-item dropdown">
              <Link to="/tours" className={location.pathname.includes('/tours') ? 'active' : ''}>
                
              </Link>
              <ul className="dropdown-menu">
                <li><Link to="/tours/hot"> </Link></li>
                <li><Link to="/tours/europe"></Link></li>
                <li><Link to="/tours/asia"></Link></li>
                <li><Link to="/tours/russia"></Link></li>
              </ul>
            </li>
            
            <li className="header-menu-item">
              <Link to="/about" className={isActive('/about') ? 'active' : ''}>
                 
              </Link>
            </li>
            
            <li className="header-menu-item">
              <Link to="/contacts" className={isActive('/contacts') ? 'active' : ''}>
                
              </Link>
            </li>
          </ul>
        </nav>
        
        <div className="header-auth">
          {isAuthenticated ? (
            <div className="header-user-menu">
              <span className="header-username">, {getFullName()}!</span>
              <div className="header-dropdown">
                <button className="header-dropdown-btn"> </button>
                <div className="header-dropdown-content">
                  <Link to="/profile"> </Link>
                  <Link to="/orders"> </Link>
                  <Link to="/support"></Link>
                  {user?.role?.name === 'admin' && <Link to="/admin"> </Link>}
                  {user?.role?.name === 'support' && <Link to="/support/tickets"> </Link>}
                  <button onClick={handleLogout}></button>
                </div>
              </div>
            </div>
          ) : (
            <div className="header-auth-buttons">
              <Link to="/login" className="header-login-btn"></Link>
              <Link to="/register" className="header-register-btn"></Link>
            </div>
          )}
        </div>
        
        <div 
          className="mobile-menu-toggle"
          onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
        >
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
      
      <div className={`mobile-menu ${mobileMenuOpen ? 'active' : ''}`}>
        <div className="container">
          <ul>
            <li><Link to="/"></Link></li>
            <li><Link to="/tours"></Link></li>
            <li><Link to="/about"> </Link></li>
            <li><Link to="/contacts"></Link></li>
            {isAuthenticated ? (
              <>
                <li><Link to="/profile"> </Link></li>
                <li><Link to="/orders"> </Link></li>
                <li><Link to="/support"></Link></li>
                {user?.role?.name === 'admin' && <li><Link to="/admin"> </Link></li>}
                {user?.role?.name === 'support' && <li><Link to="/support/tickets"> </Link></li>}
                <li><button onClick={handleLogout}></button></li>
              </>
            ) : (
              <>
                <li><Link to="/login"></Link></li>
                <li><Link to="/register"></Link></li>
              </>
            )}
          </ul>
        </div>
      </div>
    </header>
  );
};

export default Header; 